body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

#root{
  height: 100%;
}

input:focus {
  outline: none;
  padding-left: 10px;
  padding-right: 10px;
}

@font-face {
  font-family: Newsense;
  src: url(/Newsense.otf);
}
.App {
  text-align: center;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

#challenge-title{
    color: #f70893;
    font-family: "Newsense", sans-serif;
    font-size: 100px;
    width: 100%;
    text-align: center;
}

.black-background-container{
    position: fixed;
    display: flex;
    flex-direction: column;
    justify-content: center;
    height: 100%;
    width: 100%;
    background: #000000;
}

h1{
    background: #30f5c7;
    color: #ff51d7;
    padding: 10px 0px 10px 0px;
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    margin: 0 auto;
}


.colored-text-container{
    text-align: justify;
    width: 80%;
    margin: 0 auto;
    background: #292929;
    padding: 20px;
    border-radius: 4px;
    color: #b7b7b7;
}

.darker-text{
    color: white;
    font-size: 22px;
}

.lighter-text{
    color: #00ffc8;
    font-weight: 700;
    letter-spacing: 1px;
}

#answer-form{
    text-align: justify;
    width: 70%;
    margin: 0 auto;
}

#answer-form input{
    width: 100%;
    background: transparent;
    height: 73px;
    border-radius: 4px;
    font-weight: 600;
    box-shadow: none;
    border: 2px solid #00ffc8;
    color: #ffff00;
    font-size: 70px;
    padding: 10px;
    padding-bottom: 20px;
    padding-top: 20px;
}

