#challenge-title{
    color: #f70893;
    font-family: "Newsense", sans-serif;
    font-size: 100px;
    width: 100%;
    text-align: center;
}

.black-background-container{
    position: fixed;
    display: flex;
    flex-direction: column;
    justify-content: center;
    height: 100%;
    width: 100%;
    background: #000000;
}

h1{
    background: #30f5c7;
    color: #ff51d7;
    padding: 10px 0px 10px 0px;
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    margin: 0 auto;
}


.colored-text-container{
    text-align: justify;
    width: 80%;
    margin: 0 auto;
    background: #292929;
    padding: 20px;
    border-radius: 4px;
    color: #b7b7b7;
}

.darker-text{
    color: white;
    font-size: 22px;
}

.lighter-text{
    color: #00ffc8;
    font-weight: 700;
    letter-spacing: 1px;
}

#answer-form{
    text-align: justify;
    width: 70%;
    margin: 0 auto;
}

#answer-form input{
    width: 100%;
    background: transparent;
    height: 73px;
    border-radius: 4px;
    font-weight: 600;
    box-shadow: none;
    border: 2px solid #00ffc8;
    color: #ffff00;
    font-size: 70px;
    padding: 10px;
    padding-bottom: 20px;
    padding-top: 20px;
}

